.accordion {
  width: 100%;
  padding: 0;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.accordion__heading {
  font-size: 12px;
}

.accordion__content {
  display: none;
}

.accordion__content--visible {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
}