@import url(https://fonts.googleapis.com/css?family=Montserrat:100);
body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

h1 {
  font-size: 14px;
}
.accordion {
  width: 100%;
  padding: 0;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.accordion__heading {
  font-size: 12px;
}

.accordion__content {
  display: none;
}

.accordion__content--visible {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
}
.card {
  display: block;
  width: 150px;
}

@media (max-width: 500px) {
  .card {
    width: 100px;
  }
}

.card__image {
  width: 100%;
}
